
//https://www.npmjs.com/package/react-switch
import React, { Component } from 'react'
import { connect } from 'react-redux';
import Switch from "react-switch";
import {actionToggleBitAdjust} from '../../../redux/actions/bitAdjustAction'

class BitAdjustToggle2 extends Component {
    handleBitAdjustClick=()=>{
        this.props.actionToggleBitAdjust({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            height:this.props.height,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text:this.props.text,
            type:this.props.type,
            status:this.props.status,
        })
    }

    render() {
        const style = {
            position: 'absolute',
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
        }
        return (
            <div style={style}>
                <Switch height width
                    height={parseInt(this.props.height)}
                    width={parseInt(this.props.width)}
                    onChange={this.handleBitAdjustClick}
                    checked={this.props.status}
                    className="react-switch"
                />
            </div>
        );
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleBitAdjust: (item) => {
        dispatch(actionToggleBitAdjust(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(BitAdjustToggle2);